.TodoSearch{
    background: #f9fbfc;
    border-radius: 2px;
    border: 2px solidrgb(0 124 212 / 50%);
    margin: 0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align: center;
    font-family: 'Montserrat';
    font-weight: 400;
    color: #1e1e1f;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
}

.TodoSearch::placeholder{
    color: #a5a5a5;
    font-family: 'Montserrat';
    font-weight: 400;
}

.TodoSearch:focus{
    outline-color: #1b6878;
}

.TodoSearch:disabled{
    opacity: 25%;
}